.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.header {
  margin-top: 30px;
  margin-bottom: 30px;
}
.jumbotron{
  padding-top: 20px;
  padding-bottom: 20px;
  border-top: 5px solid #134454;
}
table {
  position: relative;
  width: 100%;
  color : #3b3c3d;
}
table tr td {
  position: relative;
  width : 50%;
}
.right {
  position : absolute;
  right: 0;
}
table tr td:hover {
  background: -webkit-gradient(linear, left top, right top, from(#134454), to(#1a5d72));
  background: -webkit-linear-gradient(left, #134454, #1a5d72);
  background: -o-linear-gradient(left, #134454, #1a5d72);
  background: linear-gradient(to right, #134454, #1a5d72);
  color: white;
}
.cart {
  text-align : center;
}


